"use client"

import { useState } from "react"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table"
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/components/ui/dialog"
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select"
import type { Employee } from "@/lib/types"

const branches = [
  "Sucursal Central",
  "Sucursal Norte",
  "Sucursal Sur",
]

const roles = ["Cajero", "Gerente", "Superusuario"] as const

export default function EmpleadosPage() {
  const [employees, setEmployees] = useState<Employee[]>([])
  const [newEmployee, setNewEmployee] = useState<Omit<Employee, "id">>({
    nombre: "",
    email: "",
    rol: "Cajero",
    sucursal: "",
    username: "",
    password: ""
  })

  const handleAddEmployee = (e: React.FormEvent) => {
    e.preventDefault()
    
    if (!newEmployee.nombre || !newEmployee.email || !newEmployee.sucursal) {
      alert("Por favor complete todos los campos requeridos")
      return
    }

    const employee: Employee = {
      id: Date.now(),
      ...newEmployee,
    }

    setEmployees([...employees, employee])
    setNewEmployee({
      nombre: "",
      email: "",
      rol: "Cajero",
      sucursal: "",
      username: "",
      password: ""
    })
  }

  return (
    <div className="container mx-auto py-8">
      <div className="flex justify-between items-center mb-8">
        <div>
          <h1 className="text-3xl font-bold">Gestión de Empleados</h1>
          <p className="text-gray-600 mt-2">
            Administra los empleados y sus roles
          </p>
        </div>
        <Dialog>
          <DialogTrigger asChild>
            <Button>Nuevo Empleado</Button>
          </DialogTrigger>
          <DialogContent className="sm:max-w-[600px]">
            <DialogHeader>
              <DialogTitle>Añadir Nuevo Empleado</DialogTitle>
            </DialogHeader>
            <form onSubmit={handleAddEmployee} className="space-y-4">
              <div className="grid grid-cols-2 gap-4">
                <div>
                  <Label htmlFor="nombre">Nombre</Label>
                  <Input
                    id="nombre"
                    value={newEmployee.nombre}
                    onChange={(e) =>
                      setNewEmployee({ ...newEmployee, nombre: e.target.value })
                    }
                    required
                  />
                </div>
                <div>
                  <Label htmlFor="email">Email</Label>
                  <Input
                    id="email"
                    type="email"
                    value={newEmployee.email}
                    onChange={(e) =>
                      setNewEmployee({ ...newEmployee, email: e.target.value })
                    }
                    required
                  />
                </div>
              </div>

              <div className="grid grid-cols-2 gap-4">
                <div>
                  <Label htmlFor="username">Usuario</Label>
                  <Input
                    id="username"
                    value={newEmployee.username}
                    onChange={(e) =>
                      setNewEmployee({ ...newEmployee, username: e.target.value })
                    }
                    required
                  />
                </div>
                <div>
                  <Label htmlFor="password">Contraseña</Label>
                  <Input
                    id="password"
                    type="password"
                    value={newEmployee.password}
                    onChange={(e) =>
                      setNewEmployee({ ...newEmployee, password: e.target.value })
                    }
                    required
                  />
                </div>
              </div>

              <div className="grid grid-cols-2 gap-4">
                <div>
                  <Label htmlFor="rol">Rol</Label>
                  <Select
                    value={newEmployee.rol}
                    onValueChange={(value: "Cajero" | "Gerente" | "Superusuario") =>
                      setNewEmployee({ ...newEmployee, rol: value })
                    }
                  >
                    <SelectTrigger>
                      <SelectValue placeholder="Seleccionar rol" />
                    </SelectTrigger>
                    <SelectContent>
                      {roles.map((rol) => (
                        <SelectItem key={rol} value={rol}>
                          {rol}
                        </SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                </div>
                <div>
                  <Label htmlFor="sucursal">Sucursal</Label>
                  <Select
                    value={newEmployee.sucursal}
                    onValueChange={(value) =>
                      setNewEmployee({ ...newEmployee, sucursal: value })
                    }
                  >
                    <SelectTrigger>
                      <SelectValue placeholder="Seleccionar sucursal" />
                    </SelectTrigger>
                    <SelectContent>
                      {branches.map((branch) => (
                        <SelectItem key={branch} value={branch}>
                          {branch}
                        </SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                </div>
              </div>

              <Button type="submit" className="w-full">
                Guardar Empleado
              </Button>
            </form>
          </DialogContent>
        </Dialog>
      </div>

      <Card className="p-6">
        <Table>
          <TableHeader>
            <TableRow>
              <TableHead>Nombre</TableHead>
              <TableHead>Email</TableHead>
              <TableHead>Rol</TableHead>
              <TableHead>Sucursal</TableHead>
              <TableHead>Acciones</TableHead>
            </TableRow>
          </TableHeader>
          <TableBody>
            {employees.map((employee) => (
              <TableRow key={employee.id}>
                <TableCell>{employee.nombre}</TableCell>
                <TableCell>{employee.email}</TableCell>
                <TableCell>{employee.rol}</TableCell>
                <TableCell>{employee.sucursal}</TableCell>
                <TableCell>
                  <Button
                    variant="destructive"
                    size="sm"
                    onClick={() =>
                      setEmployees(employees.filter((e) => e.id !== employee.id))
                    }
                  >
                    Eliminar
                  </Button>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </Card>
    </div>
  )
}
